<template>
    <div style="width:100%; height:100%;">

        <!-- {{dots}} -->
        <svg :height="svg_size.w*0.6" :width="svg_size.w">
            <circle 
                v-for="(dot, key) in dots" 
                :key="key" 
                :cx="dot.x" 
                :cy="dot.y" 
                :r="dot.r" 
                stroke="black" 
                stroke-width="1" 
                fill="black"
            />
            <line 
                v-for="(item, i) in svg_data.length-1" 
                :key="'line_' + i" 
                :x1="svg_data[i].x+9"
                :y1="svg_data[i].y+9" 
                :x2="svg_data[i+1].x+9" 
                :y2="svg_data[i+1].y+9" 
                style="stroke:rgb(255,0,0);stroke-width:2" 
            />
            <line 
                :x1="svg_data[0].x+9"
                :y1="svg_data[0].y+9" 
                :x2="svg_data[svg_data.length-1].x+9" 
                :y2="svg_data[svg_data.length-1].y+9" 
                style="stroke:rgb(255,0,0);stroke-width:2" 
            />
            Sorry, your browser does not support inline SVG.  
        </svg>
  

        <div class="corners-container">
            <Moveable
                v-for="(corner, key) in svg_data"
                :key="key"
                :class="'moveable corner_' + key"
                :style="`left:${corner.x}px;` + `top:${corner.y}px;`"
                v-bind="moveable"
                @drag="handleDrag"
            >
                <div class="corner_points"><font-awesome-icon :icon="['far', 'dot-circle']" />{{key+1}}</div>
            </Moveable>
            <Moveable
                :class="'moveable_op'"
                :style="`left:${get_op.x}px;` + `top:${get_op.y}px;`"
                v-bind="moveable"
                @drag="handleDragOp"
            >
                <div class="op_point"><font-awesome-icon :icon="['far', 'dot-circle']" /></div>
            </Moveable>
        </div>
    </div>
</template>



<script>

/* eslint-disable */

    import Moveable from "vue-moveable";

    export default {
        name: 'svg_component',
        props: {
            value: Array,
            svg_size: Object,
            current_pos: Object,
        },
        components: {
            Moveable
        },
        data: () => ({
            active_id: -1,
            moveable: {
                draggable: true,
                throttleDrag: 0,
                resizable: false,
                throttleResize: 1,
                keepRatio: false,
                scalable: false,
                throttleScale: 0,
                rotatable: false,
                throttleRotate: 0,
                pinchable: false, // ["draggable", "resizable", "scalable", "rotatable"]
            }
        }),
        computed: {
            svg_data() {
                 return  this.$store.getters.get_corners;
            }, 
            get_op() {
                return  this.$store.getters.get_op;
            },
            dots() {
                return  this.$store.getters.get_dots;
            }
        },
        mounted() {

        },
        methods: {


            handleDrag({ target, left, top }) {
                
                let active_id = target.getAttribute("class").split(' ')[1].split('_')[1];

                if (left < 0 ) { left = 0; } if (left > this.svg_size.w - 18) { left = (this.svg_size.w - 18); }
                if ( top < 0 ) { top = 0; }  if (top > (this.svg_size.h - 18)) { top = (this.svg_size.h - 18); }

                // console.log('onDrag left, top', target, left, top);
                target.style.left = `${left}px`;
                target.style.top = `${top}px`;


                this.$store.dispatch('update_corner', {index: active_id, value: {x :left, y: top, r: 5}})

            },

            handleDragOp({ target, left, top }) {

                target.style.left = `${left}px`;
                target.style.top = `${top}px`;


                this.$store.dispatch('update_op', { x :left, y: top })
                this.$store.dispatch('update_cp', { x :left, y: top })
                
            }
        }
    }

</script>

<style lang="scss">

    .corners-container {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
    }

    .moveable {
        position: absolute;
    }

    .moveable_op {
        position: absolute;
    }

    .moveable-control-box {
        display: none !important;
    }
    .corner_points {
        width: 18px;
        height: 18px;
        border-radius: 9px;
        border: 1px solid #62aeec;
        background: lightblue;
        svg {
            margin-left: 0px;
            margin-bottom: 4px;
            color: #3daaaf;
        }
    }
    .op_point {
        // width: 10px;
        // height: 10px;
        // border-radius: 5px;
        // border: 1px solid #62aeec;
        // background: lightblue;
        svg {
            margin-left: 0px;
            margin-bottom: 4px;
            color: #3daaaf;
        }
    }

</style>