import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({

  state: {
    corners: [
      {x: 50, y: 120},
      {x: 200, y: 120},
      {x: 150, y: 51},
    ],
    dots: [
    ],
    atractor_set: {
      ncorners: 3,
      step_coef: 0.5,
      nsteps: 1000,
    },
    op: {
      x: 450, 
      y: 230,
    }, 
    cp: {
      x: 600, 
      y: 330, 
    }
  },

  mutations: {
    update_corner(state, p) {
      Vue.set(state.corners, p.index, p.value);
    },
    rewrite_corners(state, p) {
      state.corners = p;
    },
    update_op(state, p) {
      state.op.x = p.x;
      state.op.y = p.y;
    },
    update_cp(state, p) {
      state.cp.x = p.x;
      state.cp.y = p.y;
    },
    add_corner(state, p) {
      state.corners.push(p);
    },
    add_dot(state, p) {
      state.dots.push(p);
    },
    clear_dots(state) {
      state.dots = [];
    },
    update_atractor_set(state, p) {
      Vue.set(state.corners, p.key, p.value);
    }
  },

  actions: {
    update_corner ({commit}, p) {
      commit('update_corner', p)
    },
    rewrite_corners ({commit}, p) {
      commit('rewrite_corners', p)
    },
    update_op ({commit}, p) {
      commit('update_op', p)
    },
    update_cp ({commit}, p) {
      commit('update_cp', p)
    },
    add_corner ({commit}, p) {
      commit('add_corner', p)
    },
    add_dot ({commit}, p) {
      commit('add_dot', p)
    },
    clear_dots ({commit}, p) {
      commit('clear_dots', p)
    },
    update_atractor_set ({commit}, p) {
      commit('update_atractor_set', p)
    }
  }, 


  getters: {
    get_corners: state => {
      return state.corners;
    },
    get_dots: state => {
      return state.dots;
    },
    get_op: state => {
      return state.op;
    },
    get_cp: state => {
      return state.cp;
    },
    get_atractor_set: state => {
      return state.atractor_set;
    }
  }

})
