<template>
  <div class="base_layout">
      <!-- {{cp_data}} -->
      <!-- {{store_corners}} -->
      <div class="container-full my-4">
          <div class="row">
              <div class="col-sm-4 px-4 my-2 background-secondary">
                  <div class="form-container shadowed">
                        <FormContainer 
                            v-model="atractor_set" 
                            @ncorn_change="ncorn_change"
                            @onestep_run="onestep_run"
                            @reset_field="reset_field"
                            @run_steps="run_steps"
                        >
                        </FormContainer>
                  </div>
              </div>
              <div class="col-sm-8 px-4 my-2 background-primary">
                  <div class="show-container shadowed" ref="show_container" :key="'svg_container_' + svg_key">
                      <SvgComponent v-model="store_corners" :svg_size="atractor_data.data.svg_size" :current_pos="atractor_data.current_pos"> </SvgComponent>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>

/* eslint-disable */
import FormContainer from '@/components/childs/FormContainer.vue'
import SvgComponent from '@/components/childs/SvgComponent.vue'
// import store from '../store';

export default {
    name: 'BaseLayout',
    props: {
        msg: String
    },
    components: {
        FormContainer,
        SvgComponent,
    },
    data () {
        return {
            svg_key: 1,
            atractor_data: {
                set: {
                    ncorners: 3,
                    step_coef: 0.5,
                    nsteps: 10,
                },
                data: {
                    current_pos: {
                        x: 100, 
                        y: 200,
                    },
                    corners: [
                        
                    ],
                    svg_size: {
                        w: 400,
                        h: 400,
                    }
                }
            }
        }
    },
    computed: {

        store_corners: {
            get() {
                return this.$store.getters.get_corners;
            },
            set(val) {
                this.$store.dispatch('rewrite_corners', val);
            }
        },

        atractor_set: {
            get() {
                return this.$store.getters.get_atractor_set;
            },
            set() {
                //console.log('test set');
            }
        },
        cp_data: {
            get() {
                return this.$store.getters.get_cp;
            }, 
            set(val) {
                this.$store.dispatch('update_cp', val);
            }
        }
    
    },
    methods: {
        reloadSvg() {
            console.log('reload svg', this.svg_key);
            this.svg_key++;
        },
        ncorn_change(val) {
 
            let adds = parseInt(val) - this.store_corners.length;

            let _svg_w = (this.$refs.show_container) ? this.$refs.show_container.clientWidth : this.atractor_data.data.svg_size.w;
            let _svg_h = (this.$refs.show_container) ? Math.round(this.$refs.show_container.clientWidth*0.6) : this.atractor_data.data.svg_size.h;


            for (var i of [...Array(adds).keys()]) {
                    this.$store.dispatch('add_corner', {
                        x: Math.ceil(Math.random()*_svg_w),
                        y: Math.ceil(Math.random()*_svg_h),
                    }
                );
            }

        }, 

        onestep_run() {

            //this.$store.dispatch('clear_dots');

            let _rand = Math.ceil(Math.random()*this.atractor_set.ncorners);
            let _scal = (this.atractor_set.step_coef*10)/(10-this.atractor_set.step_coef*10);

            let _dy = Math.ceil(this.cp_data.y + _scal * this.store_corners[_rand-1].y)/(1 + _scal) + 4.5;
            let _dx = Math.ceil(this.cp_data.x + _scal * this.store_corners[_rand-1].x)/(1 + _scal) + 4.5;

            console.log('set', this.cp_data.x, this.cp_data.y, this.store_corners[_rand-1].x, this.store_corners[_rand-1].y);
            console.log('set', _rand, _dx, _dy);
            
            this.$store.dispatch('add_dot', {
                x: _dx,
                y: _dy,
                r: 3,
            });

            this.$store.dispatch('update_cp', { x :_dx, y: _dy })
        }, 
        reset_field() {
            this.$store.dispatch('clear_dots');
        },
        run_steps() {

            for (var i of [...Array(parseInt(this.atractor_set.nsteps)).keys()]) {
                this.onestep_run();
            }

        }
    },

    watch: {
        atractor_data: function (val) {
            console.log('watcher!!!');
        },
    },

    created() {

       
        
    },
    mounted() {


        if (this.$refs.show_container) {
            this.atractor_data.data.svg_size = {
                w: this.$refs.show_container.clientWidth,
                h: Math.round(this.$refs.show_container.clientWidth*0.6),
            };
        }

        // this.atractor_data.data.corners = this.store_corners;
        this.atractor_data.data.corners = [
            {x: Math.ceil(this.atractor_data.data.svg_size.w * 0.7), y: Math.ceil(this.atractor_data.data.svg_size.h * 0.1)},
            {x: Math.ceil(this.atractor_data.data.svg_size.w * 0.1), y: Math.ceil(this.atractor_data.data.svg_size.h * 0.8)},
            {x: Math.ceil(this.atractor_data.data.svg_size.w * 0.9), y: Math.ceil(this.atractor_data.data.svg_size.h * 0.8)}
        ];

        this.store_corners = this.atractor_data.data.corners;
        this.cp_data = {
            x: Math.ceil(this.atractor_data.data.svg_size.w * 0.5), 
            y: Math.ceil(this.atractor_data.data.svg_size.h * 0.1)
        };
    
        
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .show-container {
        border: solid 1px #17a2b8;
        width: 100%;
        // height: 600px;
        // padding-bottom: 60%;
        border-radius: 10px;
    }

    .form-container {
        border: solid 1px #17a2b8;
        width: 100%;
        height: 100%;
        border-radius: 10px;
    }

    .shadowed {
        -webkit-box-shadow: 10px 10px 24px -8px rgba(0,0,0,0.75);
        -moz-box-shadow: 10px 10px 24px -8px rgba(0,0,0,0.75);
        box-shadow: 10px 10px 24px -8px rgba(0,0,0,0.75);
    }

</style>
