<template>
    <b-container fluid class="p-3 h-100">
        <!-- {{value}} -->
        <b-row class="my-1">
            <b-col sm="6" class="my-auto text-left">
                <label :for="`type-nangles`" class="my-0"> N corners: </label>
            </b-col>
            <b-col sm="6">
                <b-form-input :id="`type-nangles`" v-model="atractor_set.ncorners" :type="'number'" min="3" @change="ncorn_change"></b-form-input>
            </b-col>
        </b-row>
        <b-row class="my-1">
            <b-col sm="6" class="my-auto text-left">
                <label :for="`type-step_coef`" class="my-0"> Step: </label>
            </b-col>
            <b-col sm="6">
                <b-form-input :id="`type-step_coef`" v-model="atractor_set.step_coef" :type="'number'" min="0.1" max="1" step="0.1"></b-form-input>
            </b-col>
        </b-row>
        <b-row class="my-1">
            <b-col sm="6" class="my-auto text-left">
                <label :for="`type-nsteps`" class="my-0"> N Steps: </label>
            </b-col>
            <b-col sm="6" class="">
                <b-form-input :id="`type-nsteps`" v-model="atractor_set.nsteps" :type="'number'" min="1" max="100" step="1"></b-form-input>
            </b-col>
        </b-row>
        <hr>
        <div class="btn btn-info my-2 px-4 float-left" @click="onestep_run">One Step</div>
        <div class="btn btn-info my-2 px-4 float-right"  @click="run_steps">Run Steps</div>
        <div class="btn btn-secondary my-2 px-4 w-100" @click="reset_field">Reset</div>        
        <hr>
    </b-container>
</template>


<script>
 /* eslint-disable */ 

export default {
  name: 'FormContainer',
    props: {
        value: Object
    },
    computed: {
        atractor_set: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit('input', this.atractor_set);
            }
        }
    },
  components: {
    
  }, 
  methods: {
      ncorn_change(val) {
          this.$emit('ncorn_change', val);
      }, 
      onestep_run() {
          this.$emit('onestep_run');
      },
      reset_field() {
          this.$emit('reset_field');
      },
      run_steps() {
          this.$emit('run_steps');
      }
  }
}
</script>